/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

* {
	box-sizing: border-box;
}

body {
	background-color: #faf49d;
	height: 100%;
}

.container {
	position: relative;
	padding: 50px 30px 50px;
	font-family: sans-serif;
	width: 90%;
	max-width: 600px;
	margin: 140px auto;
	font-family: 'Rokkitt', serif;
}

h1 {
	font-size: 2.5em;
	text-align: center;
}

h2 {
	font-size: 2.5em;
	margin-top: 30px;
	text-align: center;
	margin-bottom: 5%;
}

.todo {
	display: flex;
	flex-direction: column;
	margin-top: 10px;
	border: 2px solid #e49651;
	border-left: none;
	border-bottom: 1px solid #e49651;
	margin: 10px auto;
}

.todo li {
	font-family: 'Rokkitt', serif;
	font-size: 1.5em;
	color: black;
	padding: 10px 5px;
	border-bottom: 1px solid #6096c8;
	border-left: 3px solid #ce674b;
	position: relative;
	animation: addedflash 0.5s 1 linear;
	order: 0;
	background-color: white;
	transition: 1s ease;
}

.todo li div {
	background-color: transparent;
	border: 0px;
	position: absolute;
	outline: none;
	font-size: 1.3em;
	top: 10px;
	right: 50px;
	cursor: default;
}

.todo li.finished {
	text-decoration: line-through red;
	background-color: #faf49d;
	animation: doneflash 0.5s 1 linear;
	order: 2;
	transition: 1s ease;
}

.todo li.important {
	background-color: #d4af37;
	order: -1;
}

.todo li.important.finished {
	order: 1;
}

.todo button {
	background-color: transparent;
	border: 0px;
	position: absolute;
	outline: none;
	font-size: 1.3em;
	top: 2px;
	right: 5px;
}

.new-item {
	border: 1px #e59651 solid;
}

.form {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}

.form .new-item {
	width: 100%;
	height: 30px;
	font-size: 1.3em;
	margin-bottom: 5%;
}

.form button {
	width: 100%;
	height: 30px;
	font-size: 1em;
	color: black;
	background-color: #00a1ff;
	border-radius: 10px;
}

.stats {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}

.stats button {
	width: 100%;
	height: 30px;
	font-size: 1em;
	background-color: #00c600;
	border-radius: 10px;
}

.stats .todo-total {
	width: 50%;
	font-size: 1.3em;
}

.stats .todo-total .total {
	color: #00a1ff;
}

.stats .completed-total {
	text-align: right;
	width: 50%;
	margin-bottom: 5%;
	font-size: 1.3em;
}

.stats .completed-total .done-total {
	color: #02c600;
}

.stats .important-total {
	font-size: 1.3em;
	margin: auto;
}

.stats .important-total .import-total {
	color: #d4af37;
}

input {
	background-color: #faf49d;
}

@keyframes addedflash {
	0% {
		background-color: white;
	}
	50% {
		background-color: #00a1ff;
	}
	100% {
		background-color: white;
	}
}

@keyframes doneflash {
	0% {
		background-color: #faf49d;
	}
	50% {
		background-color: #00c600;
	}
	100% {
		background-color: #faf49d;
	}
}
